<template>
  <div class="content">
    <h3 class="title">用户列表-新增</h3>
    <el-form ref="form" :rules="rule" class="form" :model="form">
      <el-form-item prop="username">
        <div class="flex">
          <div class="txt">账号</div>
          <el-input v-model="form.username" placeholder="请输入账号名" />
        </div>
      </el-form-item>
      <el-form-item prop="password">
        <div class="flex">
          <div class="txt">密码</div>
          <el-input v-model="form.password" placeholder="请输入密码" />
        </div>
      </el-form-item>
      <el-form-item prop="mobile">
        <div class="flex">
          <div class="txt">手机号</div>
          <el-input v-model="form.mobile" placeholder="请输入手机号码" />
        </div>
      </el-form-item>
      <el-form-item prop="name">
        <div class="flex">
          <div class="txt">姓名</div>
          <el-input v-model="form.name" placeholder="请输入真实姓名" />
        </div>
      </el-form-item>
      <el-form-item prop="role">
        <div class="flex">
          <div class="txt">角色</div>
          <el-select v-model="value" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
      </el-form-item>
      <el-form-item prop="gender">
        <div class="flex">
          <div class="txt">姓别</div>
          <el-radio-group v-model="form.gender">
            <el-radio label="男">男</el-radio>
            <el-radio label="女">女</el-radio>
          </el-radio-group>
        </div>
      </el-form-item>
      <el-form-item prop="company">
        <div class="flex">
          <div class="txt">公司</div>
          <el-input v-model="form.company" placeholder="请输入公司" />
        </div>
      </el-form-item>
      <el-form-item prop="department">
        <div class="flex">
          <div class="txt">部门</div>
          <el-input v-model="form.department" placeholder="请输入部门" />
        </div>
      </el-form-item>
      <el-form-item prop="no">
        <div class="flex">
          <div class="txt">员工编号</div>
          <el-input v-model="form.no" placeholder="请输入员工编号" />
        </div>
      </el-form-item>
      <el-form-item>
        <el-button :loading="loading" @click.native.prevent="submit">
          创建
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  export default {
    
</script>
<script>
  import {validateMobile} from "@/utils/validate";
  import {addUser} from '@/api/login'
  export default {
    data() {
      const validateUsername = (rule, value, callback) => {
        if (value.length < 2 || value.length > 5) {
          callback(new Error("账号长度在2到5之间"));
        } else {
          callback();
        }
      };
      const validatePass = (rule, value, callback) => {
        if (value.length < 5 || value.length > 16) {
          callback(new Error("密码长度在5到16之间"));
        } else {
          callback();
        }
      };
      const validatetel = (rule, value, callback) => {
        if (!validateMobile(value)) {
          callback(new Error("请输入正确的手机号"));
        } else {
          callback();
        }
      };
      return {
        options: [{
          value: '选项1',
          label: '黄金糕'
        }, {
          value: '选项2',
          label: '双皮奶'
        }, {
          value: '选项3',
          label: '蚵仔煎'
        }, {
          value: '选项4',
          label: '龙须面'
        }, {
          value: '选项5',
          label: '北京烤鸭'
        }],
        value: '',
        form: {
          username: '',
          password: '',
          mobile: '',
          name: '',
          gender: '男',
          company: '',
          department: '',
          no: '',
          role: ''
        },
        rule: {
          username: [{
            required: true,
            trigger: "blur",
            validator: validateUsername
          }],
          password: [{
            required: true,
            trigger: "blur",
            validator: validatePass
          }],
          mobile: [{
            required: true,
            trigger: "blur",
            validator: validatetel
          }],
          name: [{
            required: true,
            trigger: "blur",
            validator: validateUsername
          }],
          company: [{
            required: true,
            trigger: "blur",
            message: '请输入公司'
          }],
          department: [{
            required: true,
            trigger: "blur",
            message: '请输入部门'
          }],
          no: [{
            required: true,
            trigger: "blur",
            message: '请输入员工编号'
          }]
        },
        loading: false
      }
    },

    components: {},

    computed: {},

    mounted: function () {},

    methods: {
      submit() {
        this.$refs.form.validate(valid => {
          if (valid) {
            this.loading = true;
            console.log(this.form)
            addUser(this.form).then(res => {
              this.loading = false;
              this.$message({
                message: '提交成功',
                type: 'success'
              });
              this.form = {
                username: '',
                password: '',
                mobile: '',
                name: '',
                gender: '男',
                company: '',
                department: '',
                no: '',
                role: ''
              }
            }).catch(e => {
              this.loading = false;
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      }
    }
  }

</script>

<style lang='scss' scoped>
  .content {
    padding: 30px 60px;
    .title {
      margin-bottom: 10px;
      font-size: 16px;
      color: #fff;
        &::before {
          content: '';
          /** 定义成 block 元素 **/
          display: block;
          height: 10px;
          width: 4px;
          background: rgb(64, 158, 255);
          float: left;
          position: relative;
          top: 4px;
          margin-right: 12px;
        }
    }
    .form {
      width: 500px;
      padding: 20px 0px;
  
      .flex {
        border-bottom: 1px solid #999;
  
        .txt {
          width: 120px;
          color: rgb(64, 158, 255);
        }
  
        input {
          border: none;
        }
      }
    }
  }

</style>
<style lang="scss">
  .form {
    .el-input input {
      background: transparent;
      border: none;
      text-align: right;
      padding: 0;
      color: #333;
    }

    .el-button {
      width: 100%;
      background: var(--active) !important;
      color: #fff !important;
      border: none;
    }

    .el-form-item__error {
      top: 10px;
      left: 100%;
      width: 200px;
    }
  }

</style>



